import React, { FC } from 'react';
import { ColumnsProps, DefaultSortProps, FilterType, getExtraData, OrderType, SearchDataGrid, SearchViewProps, WindowComponent } from 'summer';
import { CLASS_NAME, GrupoProduto } from '../../../../models/dtos/ger/grupoProduto';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const GrupoProdutoSearchView: FC<SearchViewProps<GrupoProduto>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];
  return (
    <WindowComponent title="Grupo de Produtos">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'GrupoProdutoEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default GrupoProdutoSearchView;
